/* Css for all media queries are here  */

body {
	background: radial-gradient(#e5e5e5, #ffff, #e5e5e5);
	width: 100%;
	height: 100%;
	margin: 0px;
	padding: 0px;
}

.user-profile {
	font-size: medium;
	color: azure;
	font-weight: bold;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
		sans-serif;
}

#root {
	width: fit-content !important;
	display: flex !important;
}

/* FOOTER CONTENTS */

footer {
	position: relative;
	padding: 50px;
	background: #111;
	width: 100%;
}

.aboutus {
	display: flex;
}

footer h2 {
	position: relative;
	color: rgba(255, 250, 250, 0.979);
	margin-bottom: 15px;
	width: 10em;
	margin-left: 7px;
}

/* Handles the red line in the footer */
footer .container h2:before {
	content: "";
	position: absolute;
	bottom: -5px;
	width: 50px;
	height: 2px;
	background: #f00;
}

/* Handles the about us text */
footer p {
	color: #999;
	font-family: sans-serif;
}

/* Handles the footer icons */
.sci {
	margin-top: 20px;
	display: flex;
}

/* Removes the list bullets */
.sci li {
	list-style: none;
}

/* Handles the icons styling */
.sci a {
	width: 40px;
	height: 40px;
	background: #222;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 10px;
	text-decoration: none;
	border-radius: 4px;
	color: #fff;
}

.sci li a:hover {
	background: #f00;
}

/* Handles icon size at footer*/
.sci li a .fa {
	color: #fff;
	font-size: 10px;
}

/* Handles the quicklinks */
.quickLinks ul li a {
	color: #999;
	text-decoration: none;
	margin-bottom: 10px;
	display: inline-block;
	font-size: 1em;
	font-family: sans-serif;
}

.quickLinks ul li a:hover {
	color: #fff;
}

/* Controls the margin from About Us to Quicklinks */
.quickLinks {
	margin-left: 7em;
}

.contact .info li {
	margin-bottom: 10px;
	list-style: none;
	font-family: sans-serif;
}

/* Handles icons on Contact Us list */
.contact .info li span:nth-child(1) {
	color: #fff;
	margin-right: 10px;
}

.contact .info li span {
	color: #999;
}

.contact .info li a {
	color: #999;
	text-decoration: none;
}

.contact .info li a:hover {
	color: #fff;
}

.copyrightText {
	position: absolute;
	background: #181818;
	padding: 8px 100px;
	text-align: center;
	color: #999;
	width: 100%;
}


/* MEDIA QUERIES */

@media (prefers-reduced-motion: no-preference) {
	.loading-clockwise {
		animation: loading-clockwise-spin infinite 1s linear;
	}
}

.loading-clockwise {
	height: 10vmin;
	pointer-events: none;
	margin-left: 39em;
	margin-bottom: 6em;
	margin-top: 4em;
}

@keyframes loading-clockwise-spin {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}

@media (min-width: 0px) and (max-width:319.5px) {
	* {
		display: none !important;
	}

}

